import sunriseAndSunsetData from './sunrise-sunset.json';

export const getMoment = (locationName) => {
  // STEP 2ï¼å¾æ¥åºæ¥è½æéä¸­æ¾åºç¬¦åçå°å
  const location = sunriseAndSunsetData.find(
    (data) => data.locationName === locationName
  );

  // STEP 3ï¼æ¾ä¸å°çè©±åæåºé¯èª¤è¨æ¯
  if (!location) {
    throw new Error(`æ¾ä¸å° ${location} çæ¥åºæ¥è½è³æ`);
  }

  // STEP 4ï¼åå¾ç¶åæé
  const now = new Date();

  // STEP 5ï¼å°ç¶åæéä»¥ "2019-10-08" çæéæ ¼å¼åç¾
  const nowDate = Intl.DateTimeFormat('zh-TW', {
    year: 'numeric',
    month: '2-digit',
    day: '2-digit',
  })
    .format(now)
    .replace(/\//g, '-');

  // STEP 6ï¼å¾è©²å°åä¸­æ¾å°å°æçæ¥æ
  const locationDate = location?.time.find((time) => time.dataTime === nowDate);

  // STEP 7ï¼æ¾ä¸å°çè©±åæåºé¯èª¤è¨æ¯
  if (!locationDate) {
    throw new Error(`æ¾ä¸å° ${locationName} å¨ ${nowDate} çæ¥åºæ¥è½è³æ`);
  }

  // STEP 8ï¼å°æ¥åºæ¥è½ä»¥åç¶åæéè½ææéæ³è¨ï¼TimeStampï¼
  const sunriseTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunrise}`
  ).getTime();
  const sunsetTimestamp = new Date(
    `${locationDate.dataTime} ${locationDate.sunset}`
  ).getTime();
  const nowTimeStamp = now.getTime();

  // STEP 9ï¼è¥ç¶åæéä»æ¼æ¥åºåæ¥è½ä¸­éï¼åè¡¨ç¤ºçºç½å¤©ï¼å¦åçºæä¸
  return sunriseTimestamp <= nowTimeStamp && nowTimeStamp <= sunsetTimestamp
    ? 'day'
    : 'night';
};

